.child{
    margin: 0 auto;
    position: relative;
    cursor: pointer;
    border: 2px aqua solid;
    width: 80%;
    min-height: 3rem;
    border-radius: 5px;
    margin-bottom: 0.3rem;
    text-align: left;
}

.wait{
    cursor: wait;
    margin: auto;
    padding: 10% 45%;
}

.menuLink{
    padding: 1rem 2rem;
    cursor: pointer;
    text-align: center;
    color: white;
    transition: all 0.3s ease-in;
    font-size: 1.1rem;
}

.navBar{
    padding: 0 2rem;
    display: flex;
    justify-content: space-between;
    align-items: center;
    flex-wrap: wrap;
    background: aqua;
    position: absolute;
    top: 0;
    left: 0;
    right: 0;
}

.logo{
    padding: 1rem 0;
    color: white;
    font-weight: 800;
    font-size: 1.7rem;
}

.logo span{
    font-weight: 300;
    font-size: 1.3rem;
}

.menu{
    display: flex;
    justify-content: space-between;
    align-items: center;
    position: relative;
}

.container{
    max-width: 1200px;
    margin: 0 auto;
    position: relative;
    top: 4rem;
}

@media (max-width: 465px){
    .container{
        position: relative;
        top: 7rem;
    }
}
@media (max-width: 345px){
    .container{
        position: relative;
        top: 9rem;
    }
}